.listGrid {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}
.listItem {
  padding: 10px;
  border-radius: 6px;
  transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.listItem:hover {
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);
}
.button {
  position: relative;
  bottom: 10px;
  right: 10px;
}
.buttonContainer {
  padding: 50px;
}

.removeButton {
  margin-right: 10px;
  width: 100px;
  height: 30px;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.removeButton:hover {
  background-color: red;
}
.cancelButton {
  width: 100px;
  height: 30px;
}
